/*메뉴 섹션*/
.menu-sec {
  width: 100%;
  height: auto;
  padding: 0px 10px;
  display: flex;
  flex-flow: row nowrap;
  position: relative;
}
.menu-sec .container-about{
  width: 50%;
  height: auto;
  margin-right: 5px;
  position: relative;
  overflow: hidden;
}
.menu-sec .container-about .about {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: #9E62FF;
  border-radius: 80px;
  padding: 40px 40px 30px 40px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-end;
  transition: all 0.5s;
  cursor: pointer;
}

.menu-sec .container-about .about:hover {
  transform: scale(0.97);
}
.menu-sec .container-about .about .shapes{
  width: 100%;
  height: 100%;
  background-color: transparent;
  position: absolute;
  top: 0;
  left: 0;
  animation-name: spin;
  animation-duration: 8s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.menu-sec .container-about .about .shapes .rectangle {
  width: 100px;
  height: 100px;
  position: absolute;
  top: calc((100% - 250px) / 2);
  left: calc((100% - 100px) / 2);
  animation-name: spin;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  fill: #C9AEF4;
  transition: all 0.5s;
}
.menu-sec .container-about .about .shapes .circle {
  width: 100px;
  height: 100px;
  position: absolute;
  bottom: calc((100% - 200px) / 2);
  right: calc((100% - 250px) / 2);
  animation-name: spin;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  fill: #C9AEF4;
  transition: all 0.5s;
}
.menu-sec .container-about .about .shapes .triangle {
  width: 110px;
  height: 110px;
  position: absolute;
  bottom: calc((100% - 200px) / 2);
  left: calc((100% - 250px) / 2);
  transform: rotate(0deg);
  animation-name: spin;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  fill: #C9AEF4;
  transform: scale(1);
  transition: all 0.5s;
}
@keyframes spin {
  0%{ transform: rotate(0deg)}
  100%{ transform: rotate(360deg) }
}
.menu-sec .container-about .about .about-text{
  width: auto;
  min-width: 1700px;
  height: auto;
  position: absolute;
  top: calc((100% - 160px) / 2);
  color: #fff;
  font-weight: bold;
  font-size: 120px;
  z-index: 9999;
  transform: translateX(0%);
  animation-name: about-text-ani;
  animation-duration: 15s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  transition: all 0.2s;
}
.menu-sec .container-about .about .about-text-clon{
  width: 100vw;
  min-width: 1700px;
  height: auto;
  position: absolute;
  font-weight: bold;
  font-size: 120px;
  top: calc((100% - 160px) / 2);
  color: #fff;
  animation-name: about-text-ani2;
  animation-duration: 15s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  transition: all 0.2s;
}
@keyframes about-text-ani {
  0%{ transform: translateX(0%)}
  50%{ transform: translateX(-1650px)}
  50.1%{ transform: translateX(1650px)}
  100%{ transform: translateX(0%)}
}
@keyframes about-text-ani2 {
  0%{ transform: translateX(1650px)}
  100%{ transform: translateX(-1650px)}
}
.menu-sec .container-about .about:hover .about-text,
.menu-sec .container-about .about:hover .about-text-clon {
  opacity: 0;
}
